@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Roboto', sans-serif;color:#373d46;background-color:#e9e5e5;margin:0;padding:0}.container{padding:0}.card{margin:15px;height:100%;background-color:whitesmoke;border-radius:5px 5px 5px 5px;-moz-border-radius:5px 5px 5px 5px;-webkit-border-radius:5px 5px 5px 5px;box-shadow:0px 0px 5px 0px rgba(0,0,0,0.5)}.card-header *{border-radius:5px 5px 0px 0px;-moz-border-radius:5px 5px 0px 0px;-webkit-border-radius:5px 5px 0px 0px}.card-content{padding:5px;height:calc(40vh - 30px)}#profile-image{width:100%;height:100%}img{display:block;width:100%;background-color:#1b1b1b;object-fit:cover;object-position:top;height:60vh}#profile{padding:10px;background-color:whitesmoke}#profile-name #name{font-weight:700;font-size:36px}#profile-name #radius{font-weight:400;font-size:28px}#profile-location #location{padding-left:10px}#profile-description{margin-top:5px;position:relative;height:100%;overflow-y:hidden}#profile-long-description{height:100px}#actions{text-align:center;position:fixed;bottom:20px;left:0}#actions svg{height:60px;width:60px;padding:13px;margin-left:15px;margin-right:15px;border-radius:40px;background-color:whitesmoke;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.1);transition:0.4s}#actions svg.fa-times{color:#d63031}#actions svg.fa-star{color:#0984e3}#actions svg.fa-heart{color:#00b894}#actions svg:active{background-color:#c7c8ca}.swiped-up{height:calc(80vh - 30px) !important}img.min{height:20vh !important}#profile-description.maxed{overflow-y:inherit}@media (min-width: 40rem){#id{max-width:414px;max-height:904px}}@media (min-width: 80rem){#swiper{max-width:414px;max-height:705px}}@media (min-width: 120rem){#id{max-width:414px;max-height:904px}}

